* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  white-space: nowrap;
}

ul {
  list-style: none;
  display: flex;
  flex-direction: column;
  width: 100%;
}
h2 {
  user-select: none;
  font-size: 2rem;
  text-align: center;
  color: rgba(255, 255, 255, 1);
}
input {
  font-size: 1rem;
  border: none;
  outline: none;
  padding: 10px;
  border-radius: 6px;
  background-color: rgba(255, 255, 255, .6);
}

.btns {
  width: 100%;
  margin-top: 10px;

  display: flex;
  justify-content: flex-end;
}
button {
  cursor: pointer;
  padding: 6px;
  border-radius: 6px;
  font-size: 1rem;
  border: none;
  margin: 0 10px;
  color: rgba(0, 0, 0, 1);
  background-color: rgba(0, 0, 0, .2);
  user-select: none;
}
button.disabled  {
  text-decoration: line-through;
  cursor: not-allowed;
  background-color: rgba(255, 255, 255, .2);
}
button:hover {
  background-color: rgba(255, 255, 255, .8);
}

.container {
  border-radius: 6px;
  box-shadow: 5px 15px 40px 1px rgba(0,0,0,.5);
  -webkit-box-shadow: 5px 15px 40px 1px rgba(0,0,0,.5);
  -moz-box-shadow: 5px 15px 40px 1px rgba(0,0,0,.5);

  display: flex;
  overflow: hidden;
}
.container.box {
  width: 900px;
  height: 600px;
}
.container.fullscreen {
  border-radius: 0;
  width: 100vw;
  height: 100vh;
}

.sidebar {
  user-select: none;

  background-color: rgb(0, 0, 0, .6);
  color: #fff;
  max-width: 160px;
  min-width: 160px;
  width: 160px;
  height: 100%;
}
.sidebar .userInfo {
  padding: 20px 0;
  margin-bottom: 10px;
  text-align: center;
}
.sidebar .tabs {
  list-style: none;
  display: flex;
  flex-direction: column;
}
.sidebar .tabs li {
  text-decoration: none;
  padding: 10px 0;
  padding-left: 20px;
  width: 100%;
  color: rgba(255, 255, 255, .8);
  cursor: pointer;
}
.sidebar .tabs li.active {
  color: rgba(0, 0, 0, .8);
  background-color: #F2F2F1;
}
.sidebar .tabs li.active:hover {
  color: rgba(0, 0, 0, .8);
}
.sidebar .tabs li:hover {
  color: rgba(255, 255, 255, 1);
}

.content, .content-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  position: relative;
}
.content-container {
  background-color: rgb(242, 242, 241, .6);

  position: relative;
}
.client-state {
  user-select: none;
  position: absolute;
  top: 50%; left: 50%;
  transform: translate(-50%, -50%);
  animation: 2s cubic-bezier(0, -1.46, 1, 0.71) forwards hidden-p;
}
@keyframes hidden-p {
  0% {opacity: 1;}
  100% {opacity: 0;}
}

.header {
  user-select: none;
  height: 50px;
  padding: 0 10px;
  border-bottom: 1px solid #ddd;

  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.body {
  flex: 1;
  position: relative;

  display: flex;
  flex-direction: column;

  overflow: scroll;
}
ul.content-list {
  flex: 1;

  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
li.content-item {
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #ddd;
  background-color: rgb(255, 255, 255, .6);
}
li.content-item:hover,
li.content-item.clicked {
  background-color: rgb(0, 0, 0, .2);
}

table.list-info {
  position: absolute;
  bottom: 0px;
  padding: 10px;
  width: 100%;
  height: 100px;
  overflow: auto;

  /* background-image: linear-gradient(to right, rgba(0, 132, 255, 0.5) 10%, rgb(255, 255, 255, 0) 10%, #ffffff); */

  border-bottom: 1px solid #ddd;
  background-color: rgb(255, 255, 255, .6);
}
span.btn {
  color: blue;
  text-decoration: underline;
  cursor: pointer;
  margin-right: 10px;
}

#add-tasks, #config, #add-server {
  padding: 10px;
  width: 500px;
  height: 300px;
  background-color: #ddd;
  border-radius: 6px;
  box-shadow: 5px 15px 40px 1px rgba(0,0,0,0.49);
  -webkit-box-shadow: 5px 15px 40px 1px rgba(0,0,0,0.49);
  -moz-box-shadow: 5px 15px 40px 1px rgba(0,0,0,0.49);

  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;


  position: absolute;
  top: 50%; left: 50%;
  transform: translate(-50%, -50%);

  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
#add-tasks textarea,
#config .form,
#add-server table {
  user-select: none;
  flex: 1;
  width: 100%;
  border-radius: 6px;
  font-size: 1.2rem;
  color: #000;
  background-color: rgb(255, 255, 255, .5);
  padding: 10px;

  resize: none;
  border: none;
  outline: none;
  overflow: auto;
}

.server-list {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.server-list-title,
.server-list-content {
  width: 100%;
  padding: 10px 20px;
}
.server-list-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 5px;
}
.server-list-content h3 {
  text-align: center;
  padding-top: 5px;
  border-top: 1px solid rgba(0, 0, 0, .2);
}
.server-list-content ul {
  display: flex;
  flex-direction: column;
}
.server-list-content li {
  border-top: 1px solid rgba(0, 0, 0, .2);
  padding: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.server-list-content p { flex: 1; }
.server-list-content button { margin: 0 5px; }
